</template>

<script>
	export default {
		onLoad() {
			var aWebSocket = new WebSocket(this.websocketUrl + "39customer");
			uni.connectSocket({
				url: this.websocketUrl + "39"
			});

			uni.onSocketOpen(function(res) {
				console.log('WebSocket已连接！');
				uni.onSocketMessage(function(res) {
					console.log('AAA收到服务器内容：' + res.data);
				});
			});

			uni.onSocketError(function(res) {
				console.log('WebSocket断开连接！');
				console.log(JSON.stringify(res));
			});


		},
		onUnload() {
			console.log("监听页面关闭onUnload");
			uni.closeSocket();
		},
		data() {
			return {

			}
		},
		methods: {

		}
	}
</script>

